
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import commonBtn from '@/components/commonBtn'
export default {
name: 'bindTel',
components:{
XInput,XButton,commonBtn,Countdown
},
data(){
return{
title:"绑定手机号",
placeholder:"请输入手机号码",
userTel:"",
yqhBtnName:"绑定",
codeBtnName:"获取",
codeBtnStatus:false,
time:60,
}
},
methods:{
submit(){

this.codeBtnStatus = false;
this.time = 60;
},
setCode(){
this.codeBtnStatus = true;
},
finish(){
this.codeBtnStatus = false;
this.time = 60;
}
},
created () {
if(this.$route.params.type == '1'){
console.log('绑定')
}else{
this.placeholder = "请输入新手机号";
this.yqhBtnName = "修改";
this.title = "修改手机号";
}
}
}



